export class Block {
    public id: string;
    private content: string;
    private properties: Map<string, any>;
    // private links: string[];

    constructor(id: string) {
        this.id = id;
        this.content = '';
        this.properties = new Map<string, any>();
        // this.links = [];
    }

    setContent(content: string): void {
        this.content = content;
    }

    getContent(): string {
        return this.content;
    }

    toText(): string {
        let text = 'id:: ' + this.id + '\n';
        for (const [key, value] of this.properties) {
            text += key + ':: ' + value + '\n';
        }
        text += this.content;
        return text;
    }

    private readonly regex = /^[a-zA-Z0-9_\.]+:: (.*)/;

    parseText(text: string): void {
        this.id = '';
        let lines = text.split('\n');
        let contentLines: string[] = [];
        for (const line of lines) {
            const i = line.indexOf(':: ');
            if (i < 0) {
                contentLines.push(line);
            }
            else {
                const result = this.regex.exec(line);
                if (result) {
                    const key = result[1];
                    let value = result[2];
                    // if (value.startsWith('"')) {
                    // todo, toText时也需要检查是否需要带上""
                    // }
                    // else {
                    value = value.trim();
                    // }

                    if (key == 'id') {
                        if (value == '')
                            throw '非法的id: ' + line;

                        if (this.id == '')
                            this.id = value;
                        else
                            throw '重复的id: ' + this.id + ", " + line;
                    }
                    else {
                        this.properties.set(key, value);
                    }
                }
                else {
                    contentLines.push(line);
                }
            }
        }

        this.content = contentLines.join('\n');
    }
}

