import { Block } from "./block";
import { Filesystem } from "./file-system";

export class BlockDatabase {
    private fs: Filesystem;
    private id2block: Map<string, Block> = new Map<string, Block>();

    static readonly allBlockIdsPath = 'block-ids.json';

    constructor(fs: Filesystem) {
        this.fs = fs;
    }

    public async addBlock(block: Block): Promise<void> {
        if (this.id2block.get(block.id) !== undefined)
            throw 'dup block id';

        // todo: lock

        // const keysArray = Array.from(this.id2block.keys());
        const keysArray = Array.from(await this.getAllBlockIds());
        keysArray.push(block.id);
        const idsStr = JSON.stringify(keysArray);
        await this.fs.writeFileText(BlockDatabase.allBlockIdsPath, idsStr);

        const path = this.getBlockFilePath(block.id);
        await this.fs.writeFileText(path, block.toText());
    }

    public async queryBlocks(): Promise<void> {
        throw 'no impl';
    }

    public async getAllBlockIds(): Promise<string[]> {
        const content = await this.fs.readFileText(BlockDatabase.allBlockIdsPath);
        return JSON.parse(content) as string[];
    }

    public async getBlockById(id: string): Promise<Block> {
        let block = this.id2block.get(id);

        if (block === undefined) {
            block = await this.loadBlockById(id);
            if (block !== undefined)
                return block;
            throw "can't find block by id: " + id;
        }

        return block;
    }

    private async loadBlockById(id: string): Promise<Block | undefined> {
        const path = this.getBlockFilePath(id);
        if (!await this.fs.isFileExists(path)) {
            return undefined;
        }

        const content = await this.fs.readFileText(path);

        let block = new Block(id);
        if (block.id != id)
            throw 'block.id != id, ' + block.id + ", " + id + ", " + content;

        block.parseText(content);
        this.id2block.set(id, block);

        return block;
    }

    private getBlockFilePath(id: string): string {
        return 'block/' + id.substring(0, 2) + '/' + id;
    }
}