import { Spass } from "./spass";
import { Filesystem } from "./file-system";
import { BlockDatabase } from "./block-database";
import { TencentCosFilesystem } from "./tencent-cos";

export class TfApp {
    private static app = new TfApp();
    public static get(): TfApp {
        return TfApp.app;
    }

    private fs: Filesystem | undefined;
    private blockDatabase: BlockDatabase | undefined;

    async init(spass: Spass): Promise<void> {
        this.fs = new TencentCosFilesystem(spass.cosSecretId,
            spass.cosSecretKey,
            'app-tf-1253071217',
            'ap-chengdu',
            'tf-app-data/');
        this.blockDatabase = new BlockDatabase(this.fs);
    }

    public getFs(): Filesystem {
        if (this.fs === undefined)
            throw 'TfApp not init';
        return this.fs;
    }

    public getBlockDatabase(): BlockDatabase {
        if (this.blockDatabase === undefined)
            throw 'TfApp not init';
        return this.blockDatabase;
    }
}
