import React, { } from 'react';
import { Menu, Button, Layout, Space, Alert } from 'tdesign-react';
import { TfApp } from '../core/tfapp';

const { Content, Footer, Aside } = Layout;

const { SubMenu, MenuItem } = Menu;
export function StartPage() {
    return (
        <>
            <Space direction='vertical'>
                <Space
                    direction="horizontal"
                    size="medium"
                    breakLine
                >
                    <Button onClick={async () => {
                        await TfApp.get().getFs().writeFileText('test2.txt', 'abc');
                        console.log('isFileExists: ', await TfApp.get().getFs().isFileExists('test2.txt'));
                        console.log('readFile return: ', await TfApp.get().getFs().readFileText('test2.txt'));
                        await TfApp.get().getFs().deleteFile('test2.txt');
                    }}>
                        test
                    </Button>



                </Space>
                {/* <Alert theme="info" message="将完整工程添加进来，可提高Mini工程的加载速度" /> */}
            </Space>
        </>
    );
}
