import { Filesystem } from "./file-system";
import COS from 'cos-js-sdk-v5';

// tencent cos document: https://cloud.tencent.com/document/product/436/64961#81a8a2e4-fcdb-466e-9d67-310bbf63883f

export class TencentCosFilesystem implements Filesystem {
    private cos: COS;
    private bucket: string;
    private region: string;
    private pathPrefix: string;

    constructor(cosId: string, cosKey: string, bucket: string, region: string, pathPrefix: string) {
        // 推荐使用环境变量获取；用户的 SecretKey，建议使用子账号密钥，授权遵循最小权限指引，降低使用风险。子账号密钥获取可参考https://cloud.tencent.com/document/product/598/37140
        this.cos = new COS({
            SecretId: cosId,
            SecretKey: cosKey,
        });
        this.bucket = bucket;
        this.region = region;
        this.pathPrefix = pathPrefix;
    }

    async isFileExists(path: string): Promise<boolean> {
        try {
            const result = await this.cos.headObject({
                Bucket: this.bucket,
                Region: this.region,
                Key: this.pathPrefix + path
            });
            console.log('TencentCosFilesystem.isFileExists: ' + path, result);
            return result.statusCode == 200;
        }
        catch (e) {
            console.warn('TencentCosFilesystem.isFileExists: catch error: ' + path, e);
            return false;
        }
    }

    isDirectoryExists(path: string): Promise<boolean> {
        throw new Error("Method not implemented.");
    }
    getDirectoryChildren(path: string): Promise<string[]> {
        throw new Error("Method not implemented.");
    }

    async readFile(path: string): Promise<Blob | undefined> {
        try {
            const result = await this.cos.getObject({
                Bucket: this.bucket,
                Region: this.region,
                Key: this.pathPrefix + path,
                DataType: 'blob',
            });
            console.log('TencentCosFilesystem.readFile: ' + path, result);
            if (result.statusCode == 200) {
                return result.Body as Blob;
            }
            else {
                throw 'readFile fail: ' + JSON.stringify(result);
            }
        }
        catch (e) {
            console.warn('TencentCosFilesystem.readFile: catch error: ' + path, e);
            throw e;
        }
    }

    readFileText(path: string): Promise<string> {
        return new Promise((resolve, reject) => {
            this.readFile(path)
                .then((blob) => {
                    if (blob === undefined)
                        throw 'readFileText: blob === undefined, ' + path;
                    const reader = new FileReader();
                    reader.onload = () => {
                        resolve(reader.result as string);
                    };
                    reader.onerror = () => {
                        reject(reader.error);
                    };
                    reader.readAsText(blob, 'utf-8');
                })
                .catch(e => {
                    reject(e);
                });
        });
    }

    async writeFileBlob(path: string, blob: Blob): Promise<void> {
        try {
            const result = await this.cos.uploadFile({
                Bucket: this.bucket,
                Region: this.region,
                Key: this.pathPrefix + path,
                Body: blob,
                onProgress: (progressData) => {
                    console.log(JSON.stringify(progressData));
                },
            });
            console.log('TencentCosFilesystem.writeFile: ' + path, result);
            if (result.statusCode !== 200) {
                throw 'result.statusCode !== 200, ' + JSON.stringify(result);
            }
        } catch (e) {
            console.error('TencentCosFilesystem.writeFile: catch error: ' + path, e);
            throw e;
        }
    }

    async writeFileText(path: string, data: string): Promise<void> {
        const blob: Blob = new Blob([data], { type: "text/plain;charset=utf-8" });
        await this.writeFileBlob(path, blob);
    }

    async deleteFile(path: string): Promise<void> {
        try {
            const result = await this.cos.deleteObject({
                Bucket: this.bucket,
                Region: this.region,
                Key: this.pathPrefix + path,
            });
            console.log('TencentCosFilesystem.deleteFile: ' + path, result);
            if (result.statusCode !== 200 && result.statusCode != 204) {
                throw 'result.statusCode !== 200 && result.statusCode != 204, ' + JSON.stringify(result);
            }
        } catch (e) {
            console.error('TencentCosFilesystem.deleteFile: catch error: ' + path, e);
            throw e;
        }
    }

    deleteDirectory(path: string): Promise<void> {
        throw 'not impl'; // https://cloud.tencent.com/document/product/436/64963#09d81d27-36b3-48f9-b726-8429544e155c
    }
}