import React, { Fragment, useState } from 'react';
import { Menu, Button, Layout, Space, Dialog, Input, Form, Alert, Popup } from 'tdesign-react';
import { AppIcon, CodeIcon, FileIcon, FolderIcon, UserIcon, ViewListIcon, MailIcon, RollbackIcon, SettingIcon } from 'tdesign-icons-react';

import { TfApp } from '../core/tfapp'
import { setSpass, getSpass } from '../core/spass';

const { Content, Footer, Aside } = Layout;
const { SubMenu, MenuItem } = Menu;
const { FormItem } = Form;

interface InputSpassDialogProps {
    onSpassInited: () => void,
}

export function InputSpassDialog({ onSpassInited }: InputSpassDialogProps) {
    const [inputStr, setInputStr] = useState('');
    const [confirmLoading, setConfirmLoading] = useState(false);

    const onConfirm = () => {
        setConfirmLoading(true);
        console.log('点击了确认按钮');

        const items = inputStr.trim().split(',');
        setSpass({
            skey: items[0],
            cosSecretId: items[1],
            cosSecretKey: items[2],
        })

        TfApp.get().init(getSpass()).then(() => {
            setConfirmLoading(false);
            onSpassInited();
        });
    };
    const onClickCloseBtn = () => {
        console.log('点击了关闭按钮');
    };
    const onKeydownEsc = () => {
        console.log('按下了ESC');
    };
    const onClickOverlay = () => {
        console.log('点击了蒙层');
    };
    const handleClose = () => {
        console.log('关闭弹窗，点击关闭按钮、按下ESC、点击蒙层等触发');
    };

    return (
        <>
            <Dialog
                header="输入skey"
                visible={true}
                // confirmOnEnter
                confirmLoading={confirmLoading}
                cancelBtn={false}
                closeBtn={false}
                onClose={handleClose}
                onConfirm={onConfirm}
                onEscKeydown={onKeydownEsc}
                onCloseBtnClick={onClickCloseBtn}
                onOverlayClick={onClickOverlay}
                width='60%'
            >
                <Form
                    labelAlign="right"
                    layout="vertical"
                    preventSubmitDefault={true}
                    resetType="empty"
                    showErrorMessage
                >
                    <FormItem
                        initialData=""
                        label="skey"
                    // name="tel"
                    >
                        <Input onChange={setInputStr} />
                    </FormItem>
                </Form>
            </Dialog>
        </>
    );
}
