export type Spass = {
    skey: string;
    cosSecretId: string;
    cosSecretKey: string;
}

let spass: Spass = {
    skey: '',
    cosSecretId: '',
    cosSecretKey: '',
};
let spassSet = false;

export function existSpass(): boolean {
    return spassSet;
}

export function getSpass(): Spass {
    return spass;
}

export function setSpass(s: Spass) {
    spass = s;
    spassSet = true;
}
